import React from "react";
import * as LucideIcons from "lucide-react";

export type IconName = keyof typeof LucideIcons;

export interface IconProps extends LucideIcons.LucideProps {
	name: IconName;
}

export const Icon = ({ name, ...iconProps }: IconProps) => {
	const IconComponent = LucideIcons[name] as LucideIcons.LucideIcon;
	IconComponent.displayName = `${name}-icon`;
	return <IconComponent data-qa={`${name}-icon`} {...iconProps} />;
};

